@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  height: 100vh;
  background: #f4f4f4;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Roboto','sans-serif';

}
.screen{
  height: 4rem;
  margin-bottom: 4rem;

}
.Wrapper{
  background: #f6f8f9;
  width: 20rem;
  padding: 2rem;
  margin-top: 4rem;
  border-radius: 1rem;
  box-shadow: 0px 9px 15px -3px rgba(0, 0, 0, 0.2);
}
.buttonbox{
  display: grid;
  grid-template-columns: repeat(4,1fr);
  gap: .5rem;

}
.button{
  background: #e0f0f4;
  height: 3.5rem;
  padding: 10px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: black;
  border: none;
  font-size: 1.5rem;
}

.button:hover{
  border: 2px dotted #242424;
}
.equals{
  background: #4bd086;
  grid-column: 3/5;
}
.opt{
  background: #ef7c10;
}